	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Dog Food","date":"21 Mar 2002","slug":"/2002-03-21-dog-food/","tags":"Y2002"},"html":"<p><a href=\"http://www.moveabletype.org\">Moveable Type 2.0</a> has been released. Time to update <a href=\"http://www.papascott.de\">papascott.de</a> and start posting there again. I'd also like to somehow convert the happy-stripe design here to a CSS design for MT. Good thing the Easter holidays are coming up.</p>\n<p>I've stayed in FreeBSD since Monday. My system is usable, but not yet complete. My problems so far have been the java plugin for Mozilla (crashes on the Domino mail applet) and finding a Citrix client (Linux version runs under emulation, but doesn't encrypt so I can't log in).</p>"},"next":{"fields":{"title":"Dog Food","date":"18 Mar 2002","slug":"/2002-03-18-dog-food/"}},"previous":{"fields":{"title":"Resuming Transmission","date":"21 Mar 2002","slug":"/2002-03-21-resuming-transmission/"}}},{"node":{"fields":{"title":"Dog Food","date":"18 Mar 2002","slug":"/2002-03-18-dog-food/","tags":"Y2002"},"html":"<p>Time to eat my own dog food, at least at home. I administer a number of FreeBSD servers at work, but I don't have experience with a FreeBD workstation or managing a complete selection of ports. Now I'm going to try to run FreeBSD on my main workstation at home, in place of Debian GNU/Linux. I actually tried this already last year, but got frustrated keeping my ports up-to-date over my ISDN line. Now that I know about the portupgrade utility, that should be a lot easier</p>\n<p>I already downloaded the 4 CD set for FreeBSD 4.5 at work. I can install a base system in my sleep, and after a couple of tries I got the standard XFree86 3.x going for my Elsa Gladiac graphic card and USB mouse.</p>\n<p>The first packages I installed were cvsup (to keep my src and ports directories up-to-date), ncftp (to quickly find and download things) and portupgrade. I installed the kde packages from the CD (unfortunately, the supplementary kde packages are missing from the CD, so those I had to download). That gave me Konqueror and KMail, so I could surf and read mail.</p>\n<p>I then decided what basic programs I wanted to build ports for first, so I could download the distfiles overnight. I need browsers (mozilla, galeon), apache (with SSL, php, mod_perl), editors (vim, xemacs) and graphic tools (gimp, ImageMagick), and an up-to-date KDE. With 'portupgrade -NRF' (install new ports, plus all they depend on, fetch but do not build), I started downloading at 11pm, and it finished at 7:30, when Christopher I were eating breakfast.</p>"},"next":{"fields":{"title":"Bounce Bounce","date":"17 Mar 2002","slug":"/2002-03-17-bounce-bounce/"}},"previous":{"fields":{"title":"Dog Food","date":"21 Mar 2002","slug":"/2002-03-21-dog-food/"}}},{"node":{"fields":{"title":"Bounce Bounce","date":"17 Mar 2002","slug":"/2002-03-17-bounce-bounce/","tags":"Y2002"},"html":"<p>Today is the official spring opener. The patio table has been taken out of the shed and has been reassembled for your outdoor pleasure.</p>\n<p>How does a two-year-old know that it's the one day of the week his parents can sleep late, and wake up them at at 5:30?</p>\n<p>I've been trying to lose weight for the past week, but I have a feeling that the first pounds to go have been from my brain. First yesterday I bought a plane ticket from Lufthansa with the wrong return date, then today I managed to short-circuit the entire house. You see, the outtake hose on our washing machine sometimes clogs. I had to disconnect the hose, let it drain, clean out the filter, then restart the machine. Do you see the missing step? If so, you know how the connection to the extension cord got wet and tripped the main circuit breaker, which coincidentally is next to the washing machine. At least I had enough brain cells left to dry the floor before switching the power back on, otherwise I'd be sure to be up for a <a href=\"http://www.DarwinAwards.com\">Darwin award</a>!</p>\n<p>It's going to take me more than a couple weeks to lose weight, though. I'm going to be acting stupid for the next several months.</p>"},"next":{"fields":{"title":"Flyte Tyme","date":"16 Mar 2002","slug":"/2002-03-16-flyte-tyme/"}},"previous":{"fields":{"title":"Dog Food","date":"18 Mar 2002","slug":"/2002-03-18-dog-food/"}}}],"pathPrefix":"","first":false,"last":false,"index":915,"pageCount":1101}}

